import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { CrudMode } from "./crud.model";
import { Observable } from "rxjs/Observable";

@Injectable()
export class CrudService {
  constructor(private http: HttpClient) {}

  get(id = ""): Observable<Array<CrudMode>> {
    return <Observable<Array<CrudMode>>>this.http.get(`/contact/${id}`);
  }

  delete(id = "") {
    return this.http.delete(`/contact/${id}`);
  }

  put(data) {
    return this.http.put(`/contact/${data._id}`, data);
  }

  post(data) {
    return this.http.post(`/contact`, data);
  }
}



